}

.live {
    text-align: center;
    text-transform: uppercase;
    font-family: 'Poppins', sans-serif;
    color: white;
}

.covid {
    text-align: center;
    font-weight: bold;
    font-family: 'Poppins', sans-serif;;
    color: white;
}

ul {
    display: flex;
    flex-wrap: wrap;
    
  }

.card {
    margin: 20px;
    padding: 20px;
    width: 400px;
    min-height: 200px;
    display: grid;
    grid-template-rows: 20px 50px 1fr 50px;
    border-radius: 10px;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
    transition: all 0.2s;
    color: black;
    background-color:aquamarine;
  }

  .cardIndia {
    margin: 20px;
    padding: 20px;
    width: 400px;
    min-height: 200px;
    display: grid;
    grid-template-rows: 20px 50px 1fr 50px;
    border-radius: 10px;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
    transition: all 0.2s;
    color: black;
    background-image: linear-gradient(rgb(255, 22, 22),white, rgb(0, 241, 0));
  }

  .card:hover {
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
  }
  
  .card__title {
    grid-row: 3/4;
    font-weight: 500;
    color: #000000;
    text-align: center;
    font-size: 45px;
    margin-top: 10px;
    font-family: 'Poppins', sans-serif;
    text-shadow: 2px 2px 8px #575757;
  }

 

  .card__name {
      font-size: x-large;
      text-align: center;
  }

  .card__total {
